/*------------------------------------*\
  #FOOTER
\*------------------------------------*/
.footer {
  width: 100%;
  padding: 15px 0;
  margin-top: 8px;
  margin-bottom: 20px;
}

.footer__container {
  display: flex;
  justify-content: space-between;
}

.footer__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.footer__box__media {
  margin-top: 10px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 1.25rem;
}

.footer__logo {
  padding-top: 20px;
  outline: none;
  margin-bottom: 10px;
}

.footer__text {
  font-family: $font-lato;
  color: $color-white;
  font-size: 0.6875rem;
  padding-top: $padding-small;
}

@media (max-width: 1410px) {
  .footer {
    width: 100%;
    padding: 15px;
    margin: 0;
  }
  .checkbox__text {
    line-height: 2px;
  }
}
