@font-face {
  font-family: Futura;
  src: url("futura medium bt.44cf95c5.ttf");
  font-style: normal;
  font-weight: 500;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

:focus {
  outline: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

input[type="search"]::-webkit-search-cancel-button {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type="search"]::-webkit-search-results-button {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type="search"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  box-sizing: content-box;
}

textarea {
  vertical-align: top;
  resize: vertical;
  overflow: auto;
}

audio, canvas, video {
  max-width: 100%;
  display: inline-block;
}

audio:not([controls]) {
  height: 0;
  display: none;
}

[hidden] {
  display: none;
}

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  font-size: 100%;
}

a:focus {
  outline: thin dotted;
}

a:active, a:hover {
  outline: 0;
}

img {
  -ms-interpolation-mode: bicubic;
  border: 0;
}

figure, form {
  margin: 0;
}

legend {
  white-space: normal;
  border: 0;
  padding: 0;
}

button, input, select, textarea {
  vertical-align: baseline;
  margin: 0;
  font-size: 100%;
}

button, input {
  line-height: normal;
}

button, select {
  text-transform: none;
}

button, html input[type="button"], input[type="reset"], input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled], html input[disabled] {
  cursor: default;
}

input[type="checkbox"], input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}

input[type="search"]::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

textarea {
  vertical-align: top;
  overflow: auto;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html, button, input, select, textarea {
  color: #222;
}

::selection {
  text-shadow: none;
  background: #b3d4fc;
}

img {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

.chromeframe {
  color: #000;
  background: #ccc;
  margin: .2em 0;
  padding: .2em 0;
}

* {
  box-sizing: border-box;
}

html {
  background-color: #000;
}

body {
  font-family: Lato, sans-serif;
}

.container {
  width: 1352px;
  margin: 0 auto;
}

@media (width <= 1352px) {
  .container {
    width: 100%;
    padding: 0 15px;
  }
}

a {
  color: #fff;
  cursor: pointer;
  font-family: Futura;
  font-size: 1.8rem;
  text-decoration: none;
}

.title {
  letter-spacing: 1.8px;
  font-family: Futura;
  font-size: 3rem;
}

.text {
  font-family: Lato, sans-serif;
  font-size: 1.3rem;
}

.logo {
  color: #fff;
  letter-spacing: 2px;
  text-shadow: 2px 1px #ac3b01, 3px 2px #ff4d00;
  font-family: Futura;
  font-size: 2rem;
  font-weight: 900;
}

.logo:hover {
  color: #4a49ff;
}

.btn {
  color: #fff;
  cursor: pointer;
  background-color: #4a49ff;
  border: none;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  font-size: 1.1rem;
  transition: all .2s;
  display: inline-flex;
  box-shadow: 0 4px 12px #0000004d, 0 20px 13px -5px #0006, inset 1px -3px 4px #0000004d;
}

.btn:hover {
  box-shadow: 0 0 .2rem #fff, 0 0 .2rem #7092f5, 0 0 2rem #7092f5, 0 0 .8rem #7092f5, 0 0 2.8rem #7092f5, inset 0 0 1.3rem #7092f5;
}

@media (width <= 1325px) {
  .title {
    font-size: 1.8rem;
  }
}

.contact__container {
  background-color: #dededf;
  border-top: 1px solid #fff;
  border-left: 1px solid #fff;
  border-radius: 8px;
  justify-content: center;
  padding: 100px;
  display: flex;
  box-shadow: 0 4px 12px #0000004d, 0 20px 13px -5px #0006, inset 1px -3px 4px #0000004d;
}

.contact__form {
  width: 50%;
}

.contact__form--right {
  flex-basis: 440px;
  margin-bottom: 1.25rem;
}

.form__title {
  line-height: 3.6875rem;
}

.form__text {
  margin: 22px 0 42px;
  font-size: 1.2rem;
  line-height: 1.375rem;
}

.form__info {
  color: #000;
  justify-content: flex-start;
  align-items: center;
  font-weight: 500;
  display: flex;
}

.form__info__row {
  color: #000;
  justify-content: flex-start;
  align-items: center;
  font-family: Lato, sans-serif;
  font-size: 1.2rem;
  font-weight: 500;
  display: flex;
}

.info--mail:before {
  content: url("email.0c3b3ac6.png");
  height: 27px;
  margin-right: 8px;
}

.info--phone:before {
  content: url("phone-ringing.b05789d4.png");
  height: 27px;
  margin-left: 20px;
  margin-right: 8px;
}

.form, .form__row {
  flex-direction: column;
  display: flex;
}

.form__row:first-of-type {
  margin-bottom: 42px;
}

.form__input {
  background-color: #0000;
  border: none;
  border-bottom: 1px solid gray;
  border-radius: 8px 8px 0 0;
  line-height: 2.625rem;
}

.form__label {
  letter-spacing: 3px;
  color: #4a49ff;
  text-transform: uppercase;
  font-family: Futura;
  font-size: .75rem;
  font-weight: 900;
  line-height: 1rem;
}

.form__row--checkbox {
  margin-top: 30px;
}

.form__checkbox {
  cursor: pointer;
  margin-bottom: 12px;
  padding-left: 35px;
  font-size: 1.375rem;
  display: block;
  position: relative;
}

.form__checkbox input {
  opacity: 0;
  cursor: pointer;
  width: 0;
  height: 0;
  position: absolute;
}

.form__checkbox .checkbox {
  background-color: #fff;
  border: 1px solid gray;
  border-radius: 4px;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 7px;
  left: 0;
}

.form__checkbox input:checked ~ .checkbox {
  background-image: url("check-mark.78bada5f.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 75%;
}

.checkbox__text {
  color: gray;
  margin: 0 10px;
  font-family: Lato, sans-serif;
  font-size: .7rem;
  line-height: 17px;
}

.form__row--button {
  justify-content: flex-end;
  margin: 2rem 0 0;
  display: flex;
}

.btn--form {
  align-self: flex-end;
  margin-top: 30px;
  padding: 20px 97px;
  font-family: Futura;
}

@media (width <= 1020px) {
  .section__contact {
    width: 100%;
    margin: 60px auto;
  }

  .contact__container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 50px;
    display: flex;
  }

  .contact__form {
    width: 80%;
    margin-bottom: 3.8rem;
  }

  .form__row {
    margin-bottom: 1rem;
  }

  .form__row--button {
