  }
}

@media (width <= 880px) {
  .contact__form {
    width: 100%;
  }
}

@media (width <= 600px) {
  .form__info {
    flex-direction: column;
    align-items: flex-start;
  }

  .info--mail {
    margin-bottom: 1rem;
  }

  .info--phone:before {
    margin-left: .8px;
  }

  .checkbox__text {
    font-size: .6rem;
  }
}

.footer {
  width: 100%;
  margin-top: 8px;
  margin-bottom: 20px;
  padding: 15px 0;
}

.footer__container {
  justify-content: space-between;
  display: flex;
}

.footer__info {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.footer__box__media {
  justify-content: flex-end;
  align-items: center;
  gap: 1.25rem;
  margin-top: 10px;
  display: flex;
}

.footer__logo {
  outline: none;
  margin-bottom: 10px;
  padding-top: 20px;
}

.footer__text {
  color: #fff;
  padding-top: .625rem;
  font-family: Lato, sans-serif;
  font-size: .6875rem;
}

@media (width <= 1410px) {
  .footer {
    width: 100%;
    margin: 0;
    padding: 15px;
  }

  .checkbox__text {
    line-height: 2px;
  }
}

.header {
  z-index: 4;
  background: #000c;
  border-bottom: 1px solid #dededf;
  width: 100vw;
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 4px 12px #0000004d, 0 20px 13px -5px #0006, inset 1px -3px 4px #0000004d;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  height: 100%;
  display: flex;
}

.header__logo {
  font-size: 2.3rem;
}

.nav__menu {
  list-style: none;
  display: flex;
}

.menu__link {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: .1875rem;
  border-bottom: 4px solid #0000;
  padding: 28px 18px 22px;
  font-size: .8rem;
  font-weight: 700;
}

.menu__link:hover {
  color: #fff;
  border-color: #4a49ff;
}

.menu__list {
  padding: 0 26px;
}

.menu__list:last-child {
  padding-right: 0;
}

.burger__menu {
  cursor: pointer;
  border: 0;
  border-radius: 50%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 6px;
  width: 50px;
  height: 50px;
  display: none;
}

.burger__menu span {
  background: linear-gradient(#e5802d 16%, #ac3b01 64%);
  width: 30px;
  height: 3px;
}

.burger__menu strong {
  opacity: 0;
  display: none;
  position: absolute;
  top: -9999px;
  left: -9999px;
}

@media (width <= 1325px) {
  .header {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }

  .nav__menu {
    justify-content: space-between;
    display: flex;
  }

  .menu__link {
    font-size: .7rem;
  }
}

@media (width <= 900px) {
  .burger__menu {
    display: flex;
  }

  .header__menu {
    background: #000c;
    border-bottom: 1px solid #dededf;
    width: 100%;
    height: 40%;
    padding-top: 20px;
    padding-bottom: 20px;
    transition: translate .6s;
    display: flex;
    position: fixed;
    top: 68px;
    left: 0;
    translate: -110%;
    box-shadow: 0 4px 12px #0000004d, 0 20px 13px -5px #0006, inset 1px -3px 4px #0000004d;
  }

  .header__menu .nav__menu {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
  }

  .header__menu .menu__list {
    padding: 0;
  }

  .header__menu .menu__link {
    padding: 14px 20px;
    display: block;
  }

  body.show-menu .header__menu {
    translate: 0;
  }

  body.show-menu .burger__menu {
    position: relative;
  }

  body.show-menu .burger__menu span:first-of-type {
    display: none;
  }

  body.show-menu .burger__menu span {
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
  }

  body.show-menu .burger__menu span:nth-of-type(2) {
    rotate: 45deg;
  }

  body.show-menu .burger__menu span:nth-of-type(3) {
    rotate: -45deg;
  }
}

.hero {
