.section__info {
  background-color: #000;
  padding: 100px 0;
}

.info__container {
  padding: 0 100px;
}

.info__rows {
  background: #dededf;
  border: 1px solid #dededf;
  border-radius: 8px;
  justify-content: space-between;
  align-items: flex-end;
  margin: 40px 0;
  display: flex;
  box-shadow: 0 4px 12px #0000004d, 0 20px 13px -5px #0006, inset 1px -3px 4px #0000004d;
}

.info__text {
  max-width: 415px;
  margin: 80px 36px;
}

.info__text:after {
  content: "";
  background-color: #000;
  width: 290px;
  height: 4px;
  margin-top: 50px;
  display: block;
  top: 0;
  left: 0;
  translate: 30% -40%;
  rotate: -8deg;
}

.info__image {
  position: relative;
}

.info__image img {
  z-index: 2;
  position: relative;
}

.info__title {
  color: #000;
  padding-bottom: 30px;
}

.info__description {
  color: #000;
  line-height: 1.6rem;
}

.info__rows--macbook .info__image {
  margin-bottom: 38px;
  margin-right: 117px;
}

.info__rows--macbook .info__image:before {
  content: "";
  background-color: #d08af0;
  border-radius: 40px;
  width: 220px;
  height: 220px;
  display: block;
  position: absolute;
  top: -40px;
  right: -20px;
  transform: rotate(45deg);
  box-shadow: 0 4px 12px #0000004d, 0 20px 13px -5px #0006, inset 1px -3px 4px #0000004d;
}

.info__rows--macbook .info__image img {
  width: 386px;
  height: auto;
}

.info__rows--phone {
  flex-direction: row-reverse;
  padding: 1.25rem;
}

.info__rows--phone .info__image:before {
  content: "";
  background-color: #4a49ff;
  border-radius: 50%;
  width: 300px;
  height: 300px;
  display: block;
  position: absolute;
  left: 60px;
  box-shadow: 0 4px 12px #0000004d, 0 20px 13px -5px #0006, inset 1px -3px 4px #0000004d;
}

.info__rows--phone .info__image img {
  max-width: 426px;
  height: auto;
  padding-top: 20px;
}

.info__rows--phone .info__text {
  margin: 70px 0;
}

.info__double {
  display: flex;
}

.info__double > div {
  margin-bottom: 0;
}

.info__rows--part1 {
  background: #dededf;
  border: 1px solid #dededf;
  border-radius: 8px;
  flex-grow: 1;
  max-width: 620px;
  box-shadow: 0 4px 12px #0000004d, 0 20px 13px -5px #0006, inset 1px -3px 4px #0000004d;
}

.info__rows--part1 .benefits__text {
  max-width: none;
  margin: 70px 35px;
}

.info__rows--part2 {
  background: #d08af0;
  border: 1px solid #dededf;
  border-radius: 8px;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  margin-left: 60px;
  display: flex;
